import React from "react";
import {Link} from "react-router-dom";

class Map extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growText: true,
        }
    }

    handleClick() {
        this.setState(state => ({
            growText: !state.growText
        }));
    }

    render() {
        return (

            <div className={"box"}>
                <div className={"mainbodyz"}>


                            <h1>Arctic Shipping Corporation Location: </h1>
                            <hr/>
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15446.864027146103!2d121.0325206!3d14.5582261!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xd923fd79e8691f1!2sArctic%20Shipping%20Corporation!5e0!3m2!1sen!2sph!4v1620000825158!5m2!1sen!2sph"
                                width="700" height="450"  allowFullScreen="" loading="lazy"/>


                </div>
            </div>


        )
    }
}
export default Map;