import React from "react";

const Shop = () => {
    return (
        <div className="box">
            <div>
                <h1>Government Links</h1>
            </div>
            <Shopp />
        </div>
    )
};

class Shopp extends React.Component {

    render() {
        return (
            <div className={"displayz"}>
                <hr />
                <div className={"boximg"}>
                    <img src= {require('./assets/g1.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Philhealth is premier government corporation that ensures sustainable, affordable and progressive social health insurance which endeavors to influence the delivery of accessible quality health care for all Filipinos</p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g2.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Social Security System (SSS) is an agency which is created to establish a social security protection to workers in the private sector, wage earners as well as self-employed persons.
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g3.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Philippine Overseas Employment Administration (POEA) is the government agency responsible for optimizing the benefits of the country’s overseas employment program. POEA was created primarily to promote and to monitor the overseas employment of Filipino workers
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g4.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Overseas Workers Welfare Administration (OWWA) is an attached agency of the Department of Labor and Employment (DOLE), is the lead government agency tasked to protect and promote the welfare and well-being of Overseas Filipino Workers (OFWs) and their dependents
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g5.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Department of Labor and Employment (DOLE) is the prime agency of the government for the pursuit of the State's foreign policy and the nerve center for a Foreign Service worthy of the trust and pride of every Filipino
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g6.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Department of Foreign Affairs (DFA) is the prime agency of the government for the pursuit of the State's foreign policy and the nerve center for a Foreign Service worthy of the trust and pride of every Filipino.
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g7.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p>Philippine Regulatory Commision (PRC)  is mandated to promote the sustained development of a corps of competent Filipino professionals; foster internationally recognized and world-class professional service and practice; and pursue regulatory measures, programs and activities that enhance professional growth and development
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g8.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p> Phillippine Ports Authority is a government corporation specially charged with the financing, management and operations of public ports throughout the archipelago. Its goal is to implement an integrated port system that is aimed at accelerating maritime trade and commerce and carry forward the government objective of transforming the Philippines into a newly-industrialized country
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g9.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p> Technical Education and Skills Development Authority (TESDA) is the government agency tasked to manage and supervise technical education and skills development (TESDA) in the Philippines
                    </p>
                </div>
                <br/>
                <div className={"boximg"}>
                    <img src= {require('./assets/g10.jpg')} alt={"img"} width={"200"} height={"200"}/>
                    <p> MARITIME INDUSTRY AUTHORITY (MARINA) is an agency of the Philippine government under the Department of Transportation responsible for integrating the development, promotion and regulation of the maritime industry in the Philippines
                    </p>
                </div>
            </div>


        )
    }
}


export default Shop;