import React from "react";
import {Link} from "react-router-dom";

class Recruitment extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growText: true,
        }
    }

    handleClick() {
        this.setState(state => ({
            growText: !state.growText
        }));
    }

    render() {
        return (

            <div className={"box"}>
                <div className={"mainbodyz"}>
                    <div className={"boxz"}>
                        <div className={"boxzin"}>
                            <h1>Recruitment: </h1>
                            <hr/>
                            <p>Arctic Shipping Corporation is currently on the look-out for seafarers who are interested in joining our fleet.
                            </p>
                            <p>Specifically, we are in need this time for our requirements to our:</p>
                            <ol>
                                <li>Bulk Carrier Vessels (trading Worldwide)</li>
                                <li>Container Vessels (trading the Far East)</li>
                                <li>Heavy-Lift Carrier Vessels</li>
                            </ol>
                            <br/>
                            <h1>Requirements</h1>
                            <hr/>
                            <table  border="1">
                                <tbody>
                                <tr>
                                    <td><strong>Rank</strong></td>
                                    <td><strong>Requirements</strong></td></tr>
                                <tr>
                                    <td>Captain / Master</td>
                                    <td>
                                        <p>• Experience in Bulk Carrier Vessels (Handy Size, Handy Max, Panamax,   Capesize)<br />
                                            <br />
                                            • Experience in Container Vessels</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr>
                                <tr>
                                    <td>Chief Engineer</td>
                                    <td>• Experienced in Mitsui Man B&amp;W, Sulzer Engines<br />
                                        <br />
                                        • Experience in Bulk Carrier Vessels (Handy Size, Handy Max, Panamax, Capesize)<br />
                                        <br />
                                        • Experience in Container Vessels</td></tr>
                                <tr>
                                    <td>Chief Mate / Chief Officer</td>
                                    <td>
                                        <p>• Experience in Bulk Carrier Vessels (Handy Size, Handy Max, Panamax, Capesize)<br />
                                            <br />
                                            • Experience in Container Vessels</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr>
                                <tr>
                                    <td>First Engineer</td>
                                    <td>
                                        <p>• Preferably with experience in Man B&amp;ampW and Sulzer Engines </p>
                                        <p>&nbsp;</p>
                                        <p>• Experience in Bulk Carrier Vessels (Handy Size, Handy Max, Panamax, Capesize)<br />
                                            <br />
                                            • Experience in Container Vessels</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr>
                                <tr>
                                    <td>Second Engineer</td>
                                    <td>
                                        <p>• Preferably with experience in Mitsui Man B&amp;W and Sulzer Engines (Not Mandatory)</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr>
                                <tr>
                                    <td>Second Mate / Second Officer</td>
                                    <td>
                                        <p>• Experience in Bulk Carrier Vessels (Handy Size, Handy Max, Panamax, Capesize)</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr>
                                <tr>
                                    <td>Third Engineer</td>
                                    <td>
                                        <p>• Preferebly experienced with Yanmar and Daihatsu Auxiliary Engines</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr>
                                <tr>
                                    <td>Third Officer / Third Mate</td>
                                    <td>
                                        <p>• Experience in Bulk Carrier Vessels (Handy Size, Handy Max, Panamax, Capesize)<br />
                                            <br />
                                            • Experience in Container Vessels</p>
                                        <p>&nbsp;</p>
                                        <p>• US Visa</p></td></tr></tbody></table>

                        </div>
                        <div className={"displayz"}>
                            <h1>Ranks: </h1>
                            <hr/>
                            <p>We are currently looking for</p>
                            <ul>
                                <li>Captain / Master</li>
                                <li>Chief Engineer</li>
                                <li>Chief Mate / Chief Officer</li>
                                <li>First Engineer</li>
                                <li>Second Engineer</li>
                                <li>Second Mate / Second Officer</li>
                                <li>Third Engineer</li>
                                <li>Third Officer / Third Mate</li>
                            </ul>
                            <p>If you think you are qualified, and you wish to join our fleet, view the Contact Us page</p>

                        </div>
                    </div>
                </div>
            </div>


        )
    }
}
export default Recruitment;