import React from "react";
import {Link} from "react-router-dom";

class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Name:"",
            Email:"",
            Phone:"",
            Message:"",
        }
    }
    myNameHandler = (event) => {
        this.setState({Name: event.target.value});
    };
    myMailHandler = (event) => {
        this.setState({Email: event.target.value});
    };
    myPhoneHandler = (event) => {
        this.setState({Phone: event.target.value});
    };
    myMsgHandler = (event) => {
        this.setState({Message: event.target.value});
    };
    mySubmitHandler = (event) => {
        event.preventDefault();
        if(this.state.Name === ""){
            alert("Input is Blank");
        } else {
            alert("Your Message has been recieved,  " + this.state.Name);
        }
    };

    render() {
        return (

            <div className={"box"}>
                <div className={"mainbodyz"}>
                    <div className={"boxz"}>
                        <div className={"boxzin"}>
                            <h1>Offices: </h1>
                            <hr />
                            <p>Makati, Philippines
                                Arctic Shipping Corporation (Main Office) is currently located in Makati City, Philippines.
                                Its training center, which is mainly used for the Computer-Based Training Program (CBT), is also located here.
                            </p>
                            <p>Restaurants, malls, and other recreational/entertainment activity center are all near our office which
                                makes it pretty convenient for those who want to go out after office hours.
                            </p>
                            <p>What started out as a minor venture has grown into a stable and secure organization that
                                has withstood the years. In 2003, Arctic was cited by the Philippine Overseas Employment
                                Administration (POEA) as one of the outstanding manning agencies in the country. Today,
                                Arctic continues to train potential seamen and develop them into able and proficient
                                officers that quickly advance through the ranks..</p>
                        </div>
                        <div className={"displaycontact"}>
                                <h1>Primary Contacts: </h1>
                                <hr />
                            <div className={"boximg"}>
                                <img src= {require('./assets/asc-mo.jpg')} alt={"img"} width={"218"} height={"185"}/>
                                <p>Arctic Shipping Corporation <br/>
                                    Ground Floor, Goodwill Bldg.<br/>
                                    393 Senator Gil Puyat Ave.<br/>
                                    Makati City 1200, Metro Manila<br/>
                                    Philippines<br/>

                                    Tel. No: (632) 8-896-0975 / 8-896-3706 / 8-896-8147<br/>
                                    Fax No: (632) 8-890-7049</p>
                            </div>
                            <div className={"boxzin"}>
                                <h1>Contact Form: </h1>
                                <hr />
                                <p>You may fill-up the form below and send us a message if you have <br/> any questions, inquiries and/or concerns:
                                </p>
                                <form onSubmit={this.mySubmitHandler}>
                                    <table border="0" cellPadding="5" cellSpacing="5">
                                        <tbody>
                                        <tr>
                                            <td valign="top"><strong>Name:</strong></td>
                                            <td valign="top"><input size="40" onChange={this.myNameHandler}
                                                                    type="text"/></td>
                                        </tr>
                                        <tr>
                                            <td valign="top"><strong>Email Address:</strong></td>
                                            <td valign="top"><input size="40" onChange={this.myMailHandler}
                                                type="text"
                                            /></td>
                                        </tr>
                                        <tr>
                                            <td valign="top"><strong>Phone:</strong></td>
                                            <td valign="top"><input size="40" onChange={this.myPhoneHandler}
                                                type="text"
                                            /></td>
                                        </tr>
                                        <tr>
                                            <td valign="top"><strong>Message:</strong></td>
                                            <td valign="top"><textarea rows="6" cols={40} onChange={this.myMsgHandler}/>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td align="center" colSpan="2"><input type="submit" />
                                            </td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        )
    }
}
export default Contact;