import React from "react";
import {Link} from "react-router-dom";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growText: true,
        }
    }

    handleClick() {
        this.setState(state => ({
            growText: !state.growText
        }));
    }

    render() {
        return (

            <div className={"box"}>
                <div className={"mainbodyz"}>
                    <div className={"boxz"}>
                        <div className={"boxzin"}>
                            <div className={"hoem"}>
                                <h2>The Shipping Company of Choice </h2>
                                <h4>trust our experience to navigate your business</h4>
                                <p>Join us and take advantage of the promotion program that Arctic Shipping Corporation is offering; as well as competitive salary rates and benefits.
                                </p>
                                <strong>PLUS!</strong>
                                <p>We would also like to encourage you to grab this opportunity to experience the Computer-Based Training Program that we have just implemented to further enhance your 'SKA' - Skills, Knowledge and Abilities.
                                </p>
                            </div>
                            <h2>News and Events </h2>
                            <hr />
                            <div className={"boximg"}>
                                <img src= {require('./assets/asc-mo.jpg')} alt={"img"} width={"218"} height={"185"}/>
                                <p><strong>ADVISORY</strong><br/>
                                    FAMILY DAY SCHEDULE<br/>
                                    The Arctic Shipping Corporaiton annual Seafarers' Family Day schedule for Year 2018.<br/>
                                </p>
                            </div>
                            <br />
                            <div className={"boximg"}>
                                <p><strong>Seafarer's Family Day - Davao 2016
                                </strong><br/>
                                    Seafarer's Family Day on Davao City<br/>
                                    A yearly celebration for our Seafarer's Family Day. It was held at Grand Menseng Hotel, Davao City.<br/>
                                </p>
                                <img src= {require('./assets/bannerdav16.jpg')} alt={"img"} width={"218"} height={"185"}/>
                            </div>
                            <br/>
                            <div className={"boximg"}>
                                <img src= {require('./assets/bannermindoro16.jpg')} alt={"img"} width={"218"} height={"185"}/>
                                <p><strong>Seafarer's Family Day - Mindoro 2016
                                </strong><br/>
                                    Seafarer's Family Day on Mindoro City<br/>
                                    A yearly celebration for our Seafarer's Family Day. It was held at Filipiniana Hotel, Calapan, Mindoro.
                                    <br/>
                                </p>
                            </div>
                        </div>
                        <div className={"displayz"}>
                            <img src= {require('./assets/0.png')} alt={"Title"} width={"450"} height={"350"}/>
                            <img src= {require('./assets/5.png')} alt={"Title"} width={"450"} height={"350"}/>
                            <img src= {require('./assets/ship2.gif')} alt={"Title"} width={"450"} height={"350"}/>
                        </div>
                    </div>
                </div>
            </div>


        )
    }
}
export default Home;